<template>
  <div
    class="py-4 p-st"
    :class="modeClass"
  >
    <div class="container">
      <div
        class="text-center"
        data-aos="fade"
        data-aos-once="true"
        data-aos-duration="1000"
      >
        <span
          class="title text-center"
          :class="titleClass"
        >
          About Me.
        </span>
      </div>
      <hr
        width="50%"
        :class="hrClass"
      />
      <div class="row">
        <div class="col-xl-6 col-bg-6 col-md-6 col-sm-12">
          <Timeline :data="education" :nightMode="nightMode" />
        </div>
        <div class="col-xl-6 col-bg-6 col-md-6 col-sm-12">
          <Timeline :data="experience" :nightMode="nightMode" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Timeline from "./helpers/Timeline";
import info from "../../info";

export default {
  name: "About",
  components: {
    Timeline,
  },
  props: {
    nightMode: {
      type: Boolean,
    },
    highlightMode: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    modeClass() {
      return {
        'bg-light': !this.nightMode && !this.highlightMode,
        'bg-dark2': this.nightMode,
        'text-light': this.nightMode,
        'bg-highlight': this.highlightMode,
        'text-highlight': this.highlightMode,
      };
    },
    titleClass() {
      return {
        'pgray': !this.nightMode && !this.highlightMode,
        'text-light': this.nightMode,
        'text-highlight-title': this.highlightMode,
      };
    },
    hrClass() {
      return {
        'pgray': !this.nightMode && !this.highlightMode,
        'bg-secondary': this.nightMode,
        'bg-highlight-hr': this.highlightMode,
      };
    },
  },
  data() {
    return {
      education: {
        title: "education",
        data: info.education,
      },
      experience: {
        title: "experiences",
        data: info.experience,
      },
    };
  },
};
</script>

<style scoped>
.title {
  font-size: 30px;
  font-weight: 500;
}
.bg-highlight {
  background-color: #fce4ec; /* Light pink for highlight */
}
.text-highlight {
  color: #ad1457; /* Dark pink text for highlight mode */
}
.text-highlight-title {
  color: #880e4f; /* Title color for highlight mode */
}
.bg-highlight-hr {
  background-color: #f48fb1; /* HR color for highlight mode */
}
</style>
