<template>
  <div
    :class="{ 'bg-light-mode': !nightMode, 'bg-dark-mode': nightMode }"
    class="portfolio-section"
  >
    <div class="container">
      <div class="row align-items-center">
        <!-- Left: Profile Image -->
        <div class="col-lg-6 text-center">
          <div class="image-container">
            <img :src="picture" alt="Profile Image" class="profile-image" />
          </div>
        </div>
        <!-- Right: Introduction -->
        <div class="col-lg-6">
          <div class="intro-section">
            <h1
              class="title"
              :class="{ 'text-dark-mode': nightMode, 'text-light-mode': !nightMode }"
            >
              Hi, Im {{ name }}!
            </h1>
            <p class="description" v-html="description"></p>
            <div class="social-links">
              <button
                v-for="(icon, key) in socialIcons"
                :key="key"
                :class="`btn btn-${key}`"
                @click="open(key)"
              >
                <i :class="icon"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import info from "../../info";

export default {
  name: "Home",
  props: {
    nightMode: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      picture: info.flat_picture,
      name: info.name,
      description: info.description,
      socialIcons: {
        linkedin: "fab fa-linkedin",
        github: "fab fa-github",
        behance: "fab fa-behance",
        resume: "fa fa-file",
      },
      links: {
        linkedin: info.links.linkedin,
        github: info.links.github,
        behance: info.links.behance,
        resume: info.links.resume,
      },
    };
  },
  methods: {
    open(platform) {
      window.open(this.links[platform], "_blank");
    },
  },
};
</script>
<style scoped>
/* General Styles */
.portfolio-section {
  padding: 60px 0;
  transition: background-color 0.3s ease-in-out;
}

.bg-light-mode {
  background-color: #f4f4f9;
}

.bg-dark-mode {
  background-color: #1a1a2e;
}

/* Typography */
.title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.text-light-mode {
  color: #1a1a2e;
}

.text-dark-mode {
  color: #f4f4f9;
}

.description {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 20px;
  color: inherit;
}

/* Image Styling */
.image-container {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  border: 5px solid #ddd;
  width: 250px;
  height: 250px;
  margin: auto;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.image-container:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Social Links */
.social-links {
  display: flex;
  gap: 10px;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  font-size: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
}

/* Social Button Colors */
.btn-linkedin {
  background-color: #0e76a8;
  color: white;
}

.btn-github {
  background-color: #333;
  color: white;
}

.btn-behance {
  background-color: #0057ff;
  color: white;
}

.btn-resume {
  background-color: #ff6f61;
  color: white;
}

.btn:focus {
  outline: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .title {
    font-size: 2rem;
  }

  .image-container {
    width: 200px;
    height: 200px;
  }

  .btn {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
}
</style>
